import React from 'react';
import styled from 'styled-components';

const Title = styled.p`
	font-size: 14px;
	font-weight: bolder;
	margin: 10px 20px 5px 40px;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
`
const SubTitle = styled.span`
	font-size: 14px;
	margin-left: ${ window.innerWidth > 400 ? 50 : 40 }px !important;
`
const List = styled.ul`
	margin: 5px
`
const Item = styled.li`
	font-size: 14px;
	margin-left: ${window.innerWidth<=360? '40px' : '60px'};
`
const SkillContainer = styled.div`
	min-height: 550px;
`
const Miscel = styled.div`
	font-size: 14px;
	margin-left: ${ window.innerWidth > 400 ? 50 : 40 }px !important;
`

export default () => (
	<SkillContainer>
		<Title>
			Programming
		</Title>
		<SubTitle>
			JavaScript
		</SubTitle>
		<List>
			<Item>Vanilla JS</Item>
			<Item>JSON</Item>
			<Item>JQuery</Item>
			<Item>Bootstrap</Item>
			<Item>NodeJS</Item>
			<Item>React</Item>
			<Item>Redux</Item>
		</List>
		<SubTitle>
			JAVA
		</SubTitle>
		<List>
			<Item>JSP</Item>
			<Item>Servlet</Item>
			<Item>Spring</Item>
		</List>
		<Title>
			Others
		</Title>
		<Miscel>
			OracleDB, MongoDB, MSSQL, MySQL, Weblogic, Hibernate, Webpack, Git, CSS, HTML
		</Miscel>
	</SkillContainer>
)



