import React from 'react';
import styled from 'styled-components';
import CopyIconBtn from 'react-icons/lib/md/content-copy';
import copy from 'copy-to-clipboard';

const data = [
	{
		subject: 'Birth',
		res: '27 September, 1993'
	},
	{
		subject: 'Gender',
		res: 'Male'
	},
	{
		subject: 'Mail',
		res: 'hoffnman0927@gmail.com'
	},
	{
		subject: 'Phone',
		res: '+852 51081271'
	},
]
const ContentContainer = styled.div`
	display: grid;
	grid-template-columns: ${ window.innerWidth > 360 ? '120px 200px 20px' : '100px 180px 20px'};
	>svg {
		display: ${ window.innerWidth > 960 ? 'inline' : 'none' };
		color: #666;
		font-size: 14px;
		margin: 10px 20px 0px 20px;
		&:hover {
			color: #ccc;
		}
	}
`
const DataType = styled.div`
	font-size: 14px;
	font-weight: bolder;
	margin: 10px 20px 0px 40px;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
`
const Output = styled.div`
	font-size: 14px;
	margin: 10px 20px 0px 20px;
	text-align: left;
`
// need to light green  when copy btn clicked

export default () => {
	return (
		<div>
			{
				data.map( (x, cur) => {
					return (
						<ContentContainer key={cur}>
						<DataType>
							{x.subject}
						</DataType>
						<Output>
							{x.res}
						</Output>
						<CopyIconBtn onClick={ () => {
							copy(x.res);
						}} />
						<br/>
						</ContentContainer>
					)
				})
			}
		</div>
	)
}



